@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: 'Kanit', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  background-color: whitesmoke;
}

.App > main {
  position: relative;
  background-color: whitesmoke;

  display: grid;
  grid-template-columns: minmax(300px, 500px);
  justify-content: center;
  align-items: center;
}

main > button {
  width: 100%;
}

.QRtxt {
  padding-top: 1em;
  padding-bottom: 1em;
}

.control {
  position: absolute;
  top: 0;

  min-height: 100vh;
  width: 100%;
  display: grid;
  grid-template-columns: minmax(300px, 500px);
  justify-content: center;
  align-items: center;
}

.control-main {
  padding: 1.5em;
  display: grid;
  grid-column-gap: 0.5em;
  grid-template-columns: repeat(2, calc(100vw/2 - 1.5em)); 
  grid-template-areas: 
    "image title"
    "image title"
    "button btton";

  color: white;
}

.control-main > label:nth-child(1) {
  align-self: auto;
  justify-self: start;
}

.button-box {
  padding-top: 0.5em;
  padding-bottom: 0.5em;
}

.button-box button {
  width: 100%;
  color: white;
  background-color: rgb(184, 2, 154);
  border-radius: 1rem;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.bg{position:relative;background-color:#ee31ad;overflow:hidden;min-height:100vh}.bg>img{position:absolute}.bg-AB{left:20px;top:-10px;width:170px}.bg-A{left:-35px;bottom:-1px;width:150px}.bg-O{right:3%;bottom:0;width:150px}.bg-B{right:-50px;width:180px;top:-20px}

.BarcodeReader>div{position:relative}.BarcodeReader video{position:absolute;left:0}.BarcodeReader canvas{position:absolute;top:0;left:0;object-fit:contain}

